import marked from 'marked'
import hljs from 'highlight.js/lib/core'
import javascript from 'highlight.js/lib/languages/javascript'
import 'highlight.js/styles/atom-one-dark.css'

const highlightCode = () => {
  const preEl = document.querySelectorAll('pre')

  preEl.forEach((el) => {
    hljs.highlightBlock(el)
  })
}

export default {
  data(){
    return {
      // timer:
      content:''      
    }
  },
  mounted(){
    this.timer = null
    this.bindEvents()
    hljs.registerLanguage('javascript', javascript)
    marked.setOptions({
      rendered: new marked.Renderer(),
      // highlight(code, language){
      //       // highlightCode()

      //   if (hljs.getLanguage(language)) {
      //     return hljs.highlight(language, code).value
      //   }
      // }
    })
  },
  computed:{
    compiledContent(){
      return marked(this.content, {})
    }
  },
  updated: function () {
      highlightCode()
  },
  // lodash/debounce

  methods:{
    bindEvents(){
      this.$refs.editor.addEventListener('paste',async e=>{
        const files = e.clipboardData.files
        console.log(files)
        // 直接上传
      })
      this.$refs.editor.addEventListener('drop', async e=>{
        const files = e.dataTransfer.files
        console.log(files)
        // @todo 文件上传
        e.preventDefault()
      })
    },
    update(e){
      clearTimeout(this.timer)
      this.timer = setTimeout(()=>{
        this.content = e.target.value
      },350)
    },
    async submit(){
      // 文章列表，点赞，关注，草稿
      // user =》 aticle  一对多
      let ret = await this.$http.post('/article/create', {
        content:this.content, //  selected:false
        compiledContent:this.compiledContent // 显示只读取这个
      })
      if(ret.code==0){
        this.$notify({
          title:'创建成功',
          type:'success',
          message:`文章《${ret.data.title}》创建成功`
        })
        setTimeout(()=>{
          this.$router.push({ path:'/article/'+ret.data.id})
        })
      }
    }
  }
}
